@import 'reset.css';
/* COMMON */

.body__container{
  font-family: 'Roboto', sans-serif;
  font-size:16px;
  font-weight: 400;
  color:#333;
}
a{
  text-decoration: none;
}

/* btn */

.btn{
  height:34px;
  background:#eee linear-gradient(to bottom, #fcfcfc, #eee);
  border:1px solid #d5d5d5;
  border-radius:4px;
  display: inline-flex;
  align-items:center;
  padding: 0 12px;
  font-size: 14px;
  font-weight: 500;
  line-height:1.5;
  cursor:pointer;
  box-sizing:border-box;
  position:relative;
  color:#333;
  
  :hover::before{
  content:'';
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background:rgba(0,0,0,.07);
  }
  
  &.btn--primary{
    border:1px solid #65b836;
    color:#fff;
    background:#55a532 linear-gradient(#91dd70,#55ae2e);
  }
}

/* input */
.input--text{
  height:34px;
  padding:0 10px;
  border:1px solid #ddd;
  border-radius:5px;
  box-sizing:borderbox;
  outline:none;
  box-shadow:inset 0 1px 2px rgba(0,0,0,0.075); 
  font-size: 16px;
  :focus{
    border-color:#51a7e8;
    box-shadow:inset 0 1px 2px rgba(0,0,0,0.075), 
                0 0 5px rgba(81,167,232,0.5);
  }
}
/* Vendor Prefix */
.input--text::-webkit-input-placeholder{color:#cacaca;}
.input--text::-ms-input-placeholder{color:#cacaca;}
.input--text::-moz-input-placeholder{color:#cacaca;}

/* HEADER */
header{
  border-bottom: 1px solid rgba(0,0,0,.75);
  box-shadow: 0 0 5px rgba(0,0,0,.75);
  background: #fff;
  height:auto;
  .inner{
    max-width: 980px;
    width: 900px;
    height:78px;
    margin: 0 auto;
    .menu-group{
      display: flex;
      align-items: center;
      height: 100%;
    }  
  }
}
header .logo{
  margin-right: 10px;
  a{
    display: block;
    background: url(../img/logo.svg);
    width: 32px;
    height: 32px;
    text-indent: -999em;
    :hover{
      background: url(../img/logo_on.svg);
    }
  }
  .main-menu{
    display: flex;
    li a{
      display: block;
      padding: 10px;
      color:#3c4146;
      :hover{
        color:#4078c0;
      }
    }
  }
}